let g_module;
let g_wasmBinary;
export function loadWasm() {
    if (!g_wasmBinary) {
        g_wasmBinary = extract(blobStr);
    }
    if (!g_module) {
        g_module = wrapper({
            wasmBinary: g_wasmBinary,
        });
    }
    return g_module;
}

export function unloadWasm() {
    if (g_module) {
        g_module = undefined;
    }
}
